import React, { Component } from 'react';
import { withRouter } from "react-router";
import './POAButton.css';

class POAButton extends Component {
    constructor(props) {
        super(props);
        this.state = {
            btnClass: props.btnClass,
            backgroundColor:"#7f00ff",
            buttonTextColor:"#fff"
        }
    }
    componentDidMount = () => {
        console.log(this.props.backgroundColor)
        if(this.props.backgroundColor != null){
            this.setState({backgroundColor: this.props.backgroundColor})
        }
        if(this.props.buttonTextColor != null){
            this.setState({buttonTextColor: this.props.buttonTextColor})
        }
        
        
    }

    render() {
        return (
            <div className={"POAButton "} onClick={this.props.onClick} style = {{background:this.state.backgroundColor}}>
                <p style = {{color: this.state.buttonTextColor}}>{this.props.label}</p>
            </div>
        )
    }
}

export default withRouter(POAButton);